<template>
  <div class="Category">
    <!-- 侧边导航栏 -->
    <div class="mui-content">
      <div class="mui-row">
        <div class="mui-col-xs-3 left">
          <ul>
            <li v-for="(item,i) in list" :key="i">
              <a
                :href="'#'+item.name"
                :class="{'active':activeClass==i}"
                @click="listClick(i)"
              >{{item.name}}</a>
            </li>
          </ul>
        </div>
      </div>
      <!-- 右边商品分类 -->
      <div class="mui-col-xs-9 right">
        <div class="mui-row" v-for="item in list" :key="item.id">
          <h4 :id="item.name">{{item.name}}</h4>
          <ul>
            <li class="mui-col-xs-4" v-for="val in item.sub" :key="val.id">
              <router-link
                :to="{
                path:'/list',
                query:{id:val.id}
              }"
              >
                <img :src="val.image" alt />
                <br />
                {{val.name}}
              </router-link>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Category",
  data() {
    return {
      list: [],
      activeClass: 0
    };
  },
  mounted() {
    this.$axios
      .get("http://47.93.224.9/api/category")
      .then(res => (this.list = res.data.data))
      .catch(error => console.log(error));
  },
  methods: {
    listClick(i) {
      this.activeClass = i;
    }
  }
};
</script>
<style>
* {
  scroll-behavior: smooth;
}
/* 左侧导航栏 start */
.left {
  height: auto;
  float: left;
  position: fixed;
  left: 0;
  background: #f1f5f8;
}
.left li {
  height: 50px;
}
.left a {
  color: #9ca0a3;
  text-align: center;
  line-height: 50px;
  font-size: 15px;
  letter-spacing: 2px;
  height: 50px;
  display: block;
}
.left li a.active {
  background: #ffffff;
}
.left li a.active {
  color: red;
}
.mui-row > div {
  margin: 0;
}
.mui-row > h4,
ul {
  margin: 0;
  font-size: 16px;
}
.mui-row img {
  width: 47px;
  height: 47px;
}
/* 左侧导航 end */

/* 右侧商品分类 start */
.Category .right {
  height: auto;
  background: #ffffff;
  float: left;
  padding-bottom: 55px;
  margin-left: 23%;
}
.right h4 {
  text-align: left;
  width: 90%;
  height: 20px;
  margin: 0 0 2rem 0;
  display: inline-block;
  padding-top: 50px;
}
.right .mui-row:nth-of-type(1) h4 {
  padding-top: 20px;
}
.right ul {
  height: auto;
}
.right .mui-col-xs-4 {
  float: left;
}
.right li a {
  color: #000000;
  font-size: 14px;
}
.right img {
  width: 50%;
}
</style>